#statistics {
  padding: 30px 0;
}
#statistics .container {
  background-color: #f0f2f5;
  min-height: 84vh;
  padding: 20px;
  padding-top: 0;
}
#statistics .container > i {
  margin: 10px 0;
  color: #333;
  cursor: pointer;
}
#statistics .container .datePiker {
  display: inline-block;
  width: 70%;
}
#statistics .container .datePiker .ant-calendar-picker-input {
  border: none;
}
#statistics .container .datePiker .ant-calendar-picker {
  min-width: 80px !important;
  width: 47%;
}
#statistics .container h4 {
  margin-top: 0;
  margin-bottom: .5em;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
}
#statistics .container .antd-pro-pages-dashboard-analysis-style-rankingList li {
  display: flex;
  align-items: center;
  margin-top: 16px;
  zoom: 1;
  list-style: none;
}
#statistics .container .antd-pro-pages-dashboard-analysis-style-rankingList li .antd-pro-pages-dashboard-analysis-style-rankingItemNumber {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin-top: 1.5px;
  margin-right: 16px;
  font-weight: 600;
  font-size: 12px;
  line-height: 24px;
  text-align: center;
  background-color: #f5f5f5;
  border-radius: 24px;
}
#statistics .container .antd-pro-pages-dashboard-analysis-style-rankingList li span {
  color: rgba(0, 0, 0, 0.65);
  font-size: 14px;
  line-height: 22px;
}
#statistics .container .antd-pro-pages-dashboard-analysis-style-rankingList li .antd-pro-pages-dashboard-analysis-style-active {
  color: #fff;
  background-color: #314659;
}
#statistics .container .antd-pro-pages-dashboard-analysis-style-rankingList li .antd-pro-pages-dashboard-analysis-style-rankingItemTitle {
  flex: 1 1;
  margin-right: 8px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
#statistics .container .ant-tabs-bar .ant-tabs-extra-content {
  line-height: 44px;
  text-align: right;
}
#statistics .container .ant-tabs-bar .ant-tabs-extra-content .day {
  margin-right: 15px;
  cursor: pointer;
}
#statistics .container .ant-tabs-bar .ant-tabs-extra-content .active {
  color: #1890ff;
}
#statistics .container > .ant-card > .ant-card-body {
  padding-top: 0;
}
#statistics .container .ant-card-head {
  position: relative;
}
#statistics .container p {
  color: #575757;
  font-size: 18px;
  padding-bottom: 10px;
}
#statistics .container .ant-table-content {
  border: 1px solid #e8e8e8;
  padding: 5px;
  border-radius: 4px;
}
#statistics .container .ant-table-content tr:last-child td {
  border: none;
}
#statistics .container .ant-table-content .ant-table-placeholder {
  border: none;
}
